
import { createWebHistory, createRouter } from "vue-router";
import Product from './components/ProductWrap.vue';
import Company from './components/CompanyWrap.vue';
import Service from './components/ServiceWrap.vue';
const routes = [
  {
    path: "/",
    component: Product,
  },
  {
    path: "/company",
    component: Company,
  },
  {
    path: "/service",
    component: Service,
  },
];
const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 
